<template>
        <div class="article-box">
            <ul class="article-list">
                <li v-for="(item,index) in articleList" :style="item.listStyle" @click="articleView(item)">
                    <div class="img" :style="imgStyle">
                        <img :src="item.imgSource">
                    </div>
                    <div class="subject-content">
                        <div class="category" v-if="boardCategoryUse && item.category">
                            {{ categoryNames[item.category] }}
                        </div>
                        <div class="main string-cut">
                            <font-awesome-icon class="icon" v-if="item.secret=='yes'" icon="fa-solid fa-lock" />
                            {{ item.subject }}

                        </div>
                        <div class="date">{{ item.createAt.substring(0, 16).replace('T', ' ') }}</div>
                    </div>

                </li>
            </ul>
        </div>
</template>
<script>

import UTIL from '@/common/util'
import JQ from 'jquery'
import apiService from "@/config/web/apiService";

export default {
    name: 'pcBoardGalleryList',
    props: [
        "articleData",
        "categoryNames",
        "boardCategoryUse"
    ],
    watch: {
        articleData(val) {
            this.setArticleList()
        }
    },
    data() {
        return {
            articleList: [],
            util: UTIL,
            imgStyle: {},
            imageSize:{width:200,height:200}

        };
    },
    created() {
        this.setData();
    },
    methods: {
        setData() {
            this.setArticleList()
        },
        setArticleList() {
            const cell = Math.floor(1194 / parseInt(this.imageSize.width));
            let marginRight = 1194 - (parseInt(this.imageSize.width) * cell);
            marginRight = marginRight / (cell-1);
            this.imgStyle = {'width': this.imageSize.width + 'px', 'height': this.imageSize.height + 'px'}

            let k = 1;
            this.articleList = [];
            for (const item of this.articleData) {
                let itemMargin = (k % cell == 0) ? 0 : marginRight;
                item.listStyle = {'width': this.imageSize.width + 'px', 'margin-right': itemMargin + 'px'}
                item.imgSource = (item.img)?apiService.imgHost+'api/controller/board/getArticleImage?imgName='+encodeURIComponent(item.img):'';
                this.articleList.push(item);
                k++;
            }
        },
        articleView(item) {

            this.$emit('boardAction',{type:'view',item:item})
        },

    }

}
</script>

<style lang="scss" scoped>

    .article-box {
        width: 100%;
        padding: 50px 0;

        .article-list {
            padding: 0;
            margin: 0;
            display: flex;
            flex-wrap: wrap;

            li {
                padding: 0;
                cursor: pointer;
                margin-bottom:30px;
                .img {
                    overflow: hidden;
                    img {
                        width:100%;
                        min-height:100%;
                    }
                    margin-bottom: 20px;
                }

                .subject-content {
                    .category {
                        font-weight: 700;
                        font-size: 14px;
                        line-height: 17px;
                        color: #000000;
                        padding-bottom: 10px;
                    }
                    .main {
                        font-weight: 500;
                        font-size: 16px;
                        line-height: 140%;
                        color: #111;
                        margin:0 0 10px 0;
                        max-height: 2.5em;
                        -webkit-line-clamp: 2;
                        .icon {
                            font-size:14px;
                        }
                    }
                    .date {
                        font-size: 12px;
                        line-height: 20px;
                        color: #808080;
                    }
                }

            }

        }
    }
</style>