<template>
    <div class="board-container-layout">
        <div class="board-container">
            <div class="board-title">
                {{ boardName }}
                <div class="write-button tablet-mobile-element" v-if="wauth">
                    <v-btn class="blackButton" @click="registArtice()"><span>글쓰기</span></v-btn>
                </div>

            </div>

            <div class="search-box">
                <form novalidate @submit.prevent="search">
                <ul class="search-ul">
                    <li class="select-li category" v-if="boardCategoryUse">
                        <v-select
                                outlined
                                v-model="category"
                                :items="categoryList"
                                placeholder="카테고리 선택"
                                dense
                        />
                    </li>

                    <li class="select-li">
                        <v-select
                                outlined
                                v-model="keywordCmd"
                                :items="keywordCmdList"
                                dense
                        />
                    </li>
                    <li class="input-li">
                        <div class="input-div">
                            <v-text-field
                                    outlined
                                    dense
                                    v-model="keyword"
                                    placeholder="검색어를 입력하세요"
                                    append-icon="mdi-magnify"
                                    @click:append="search()"

                            />
                        </div>
                        <input type=""
                    </li>
                    <li class="button-li pc-element">
                        <div class="write-button" v-if="wauth">
                            <v-btn class="blackButton" @click="registArtice()"><span>글쓰기</span></v-btn>
                        </div>
                    </li>
                    <input type="submit" style="display:none">
                </ul>
                </form>
            </div>

            <normalList @boardAction="boardAction" :categoryNames="categoryNames"
                        :boardCategoryUse="boardCategoryUse" :noticeData="noticeData"
                        :articleData="articleData"></normalList>
            <paging :paging="paging" :reload="pagingReload" @pageMove="pageMode"></paging>
            <articleUpassComponent :bid="bid" :articleId="articleId" @upassSuccess="viewSuccess"
                                   :modelOpen="passDialogShow"
                                   @parentOpenFalse="passDialogShow=false"></articleUpassComponent>

        </div>
    </div>
</template>
<script>

    import AUTH from '@/config/web/auth'
    import apiService from '@/config/web/apiService'
    import {
        API_URL_GET_BOARD_ARTICLE_LIST,
        API_URL_CHECK_BOARD_ARTICLE_UPASS
    } from '@/config/web/urls'
    import normalList from './skin/normalList'
    import galleryList from './skin/galleryList'
    import paging from '@/components/paging'

    import articleUpassComponent from '@/components/articleUpass'

    export default {
        name: 'articleList',
        components: {
            normalList,
            galleryList,
            articleUpassComponent,
            paging
        },
        watch: {
            '$route'(to, from) {
                this.dataInit();
                this.setInit();
            }
        },
        data() {
            return {
                passDialogShow: false,
                articleData: [],
                noticeData: [],
                boardData: {},
                limit: 12,
                page: 1,
                total: 0,
                pageGroup: 5,
                paging: {},
                pagingReload: false,
                boardShow: '',
                bid: '',
                memberInfo: {},
                isLogin: false,
                userPass: '',
                articleId: '',
                categoryNames: {},
                boardCategoryUse: false,
                boardName: '',
                category: '',
                categoryList: [],
                serverInit: false,
                wauth: false,
                mcode: '',
                keywordCmd: 'subject',
                keywordCmdList: [
                    {text: '제목', value: 'subject'},
                    {text: '내용', value: 'content'}
                ],
                keyword: '',
            };
        },
        created() {

            this.setInit();
        },
        methods: {
            dataInit() {
                this.passDialogShow = false
                this.articleData = []
                this.noticeData = []
                this.boardData = {}
                this.page = 1
                this.total = 0
                this.paging = {}
                this.pagingReload = false
                this.boardShow = ''
                this.bid = ''
                this.memberInfo = {}
                this.isLogin = false
                this.userPass = ''
                this.articleId = ''
                this.categoryNames = {}
                this.boardCategoryUse = false
                this.boardName = ''
                this.category = ''
                this.categoryList = []
                this.serverInit = false
                this.wauth = false
                this.mcode = ''
                this.keywordCmd = 'subject';
                this.keyword = ''
            },
            setInit() {
                this.bid = this.$route.params.bid;
                this.mcode = this.$route.params.mainCode;
                this.scode = this.$route.params.subCode;
                const member = AUTH.getSession();
                if (member && member?.memberInfo) {
                    this.memberInfo = member.memberInfo;
                    this.isLogin = true;
                }
                if (sessionStorage.getItem('tempParams')) {
                    const tempParams = JSON.parse(sessionStorage.getItem('tempParams'));
                    this.page = tempParams.page;
                    this.category = tempParams.category;
                    sessionStorage.setItem('tempParams', '');
                }
                this.getData();
            },
            search() {
              this.page = 1;
              this.getData();
            },
            getData() {
                const frm = this.getSearchParams();
                apiService.post(API_URL_GET_BOARD_ARTICLE_LIST, frm, (res) => {
                    this.total = res.articleData.total
                    this.paging = {
                        total: this.total,
                        currentPage: this.page,
                        limit: this.limit,
                        pageGroup: this.pageGroup,
                    }
                    let start = this.total - ((this.page - 1) * this.limit);
                    let k = 0;
                    const articleData = [];
                    for (let item of res.articleData.list) {
                        item.number = start - k;
                        articleData.push(item)
                        k++;
                    }
                    this.articleData = articleData;

                    this.noticeData = res.noticeData.list;

                    this.pagingReload = true;
                    setTimeout(() => {
                        this.pagingReload = false;
                    }, 100)

                    if (!this.serverInit) {
                        this.boardData = res.board;
                        this.boardName = this.boardData.bname;
                        if (this.boardData.wauth != 'admin') {
                            if (this.boardData.wauth == 'user') {
                                if (this.isLogin) this.wauth = true;
                            } else {
                                this.wauth = true;
                            }
                        } else {
                            if (this.isLogin && this.memberInfo.role == 'ROLE_ADMIN') {
                                this.wauth = true;
                            }
                        }
                        if (this.boardData.categoryUse == 'yes') {
                            this.categoryList = [];
                            const allCategoryActive = (!this.category || this.category == 'all') ? 'active' : '';
                            for (let cate of JSON.parse(this.boardData.categoryList)) {
                                if (cate.use == 'yes') {
                                    this.categoryNames[cate.code] = cate.name;
                                    this.categoryList.push({text: cate.name, value: cate.code})
                                }
                            }
                            if (this.categoryList.length > 0) {
                                this.categoryList.unshift({text: '전체', value: 'all', active: allCategoryActive})
                                this.boardCategoryUse = true;
                            }
                        }
                        this.boardShow = this.boardData.btype
                        this.serverInit = true;
                    }
                })
            },
            getSearchParams() {
                let category = '';
                if(this.category) {
                    category = (this.category=='all')?'':this.category;
                }

                return {
                    bid: this.bid,
                    category: category,
                    limit: this.limit.toString(),
                    page: this.page.toString(),
                    keywordCmd:this.keywordCmd,
                    keyword:this.keyword,
                    orderByField:'createAt',
                }
            },
            pageMode(params) {
                this.page = params.page;
                this.getData();
            },
            boardAction(params) {
                switch (params.type) {
                    case 'view':
                        this.articelView(params.item)
                        break;
                }
            },
            articelView(item) {
                let viewFlag = false;
                if (item.open == 'no') {
                    if (this.isLogin && this.memberInfo.role == 'ROLE_ADMIN') {
                        viewFlag = true;
                    } else {
                        if (item.userId) {
                            if (item.userId == this.memberInfo.uid) viewFlag = true;
                        } else {
                            this.articleId = item.id;
                            this.passDialogShow = true;
                            return;
                        }
                    }
                } else {
                    viewFlag = true;
                }
                if (viewFlag) {
                    const tempParams = {
                        page: this.page,
                        category: this.category
                    }
                    //sessionStorage.setItem('tempParams', JSON.stringify(tempParams));
                    this.$router.push('/board/articleView/' + this.mcode + '/' + this.scode + '/' + item.id)
                }
            },
            viewSuccess() {
                const tempParams = {
                    page: this.page,
                    category: this.category
                }
                //sessionStorage.setItem('tempParams', JSON.stringify(tempParams));
                this.$router.push('/board/articleView/' + this.mcode + '/' + this.scode + '/' + this.articleId)

            },
            registArtice() {
                if (this.boardData.wauth == 'user') {
                    if (!this.isLogin) {
                        let params = {
                            message: '먼저 로그인 하세요',
                        };
                        this.$eventBus.$emit('modalOpen', params);
                        return;
                    }
                }
                this.$router.push('/board/articleRegist/' + this.mcode + '/' + this.scode + '/' + this.boardData.bid + '/regist')
            },


        }

    }
</script>

<style lang="scss" scoped>
    .board-container {
        width: 100%;
        margin: 0;
        padding: 21px 24px;
        min-height: calc(100vh - 300px);
        background: #fff;

        .board-title {
            font-size: 18px;
            font-weight: bold;
            color: #333333;
            border-bottom: solid 1px #333;
            padding-bottom: 10px;
        }

        .search-box {
            width: 100%;
            padding: 20px 0 0 0;
            margin: 0;

            ul {
                padding: 0;
                margin: 0;
                display: flex;

                li {
                    &.select-li {
                        width: 127px;
                        min-width: 127px;

                        &.category {
                            width: 140px;
                            min-width: 140px;
                            margin-right: 10px;
                        }
                    }

                    &.input-li {
                        width: 90%;

                        .input-div {
                            width: 360px;
                        }
                    }

                    &.button-li {
                        width: 71px;
                        min-width: 71px;

                        .blackButton {
                            min-height: 36px;
                            max-height: 36px;

                            span {
                                font-size: 14px !important;
                                font-weight: normal !important;
                            }
                        }
                    }
                }
            }


        }

        .category-box {
            width: 100%;
            padding: 40px 0 0 0;
            margin: 0;

            ul {
                padding: 0;
                margin: 0;
                display: flex;
                justify-content: center;

                li {
                    cursor: pointer;
                    font-weight: 400;
                    font-size: 16px;
                    color: #4E4E4E;
                    margin: 0 25px;

                    &.active {
                        font-weight: bold;
                        color: #000;
                    }
                }
            }


        }

    }

    .modal-layout {
        padding: 30px;

        .pass-title {
            font-siZe: 18px;
            font-weight: bold;
            color: #000;
            padding: 20px 0 30px 0;
            text-align: center;
        }

        .input-box {

        }

        .button-row {
            display: flex;
            justify-content: space-between;

            .whiteButton {
                min-height: 40px;

                span {
                    font-size: 14px;
                }
            }

            .blackButton {
                min-height: 40px;
                max-height: 40px !important;

                span {
                    font-size: 14px;
                }
            }
        }
    }
    @media screen and (min-width: 450px) and (max-width: 1259px) {
        .board-container-layout {
            min-height: calc(100vh - 50px);
            .board-container {
                .board-title {
                    position: relative;
                    .write-button {
                        position: absolute;
                        right:0;
                        top:-4px;
                        width: 71px;
                        min-width: 71px;
                        .blackButton {
                            min-height: 35px !important;
                            max-height: 35px !important;
                            span {
                                font-size: 14px;
                            }
                        }
                    }
                }
                .search-box {
                    ul {
                        li {
                            &.select-li {
                                width:80px !important;
                                min-width:80px  !important;;

                                &.category {
                                    width: 100px !important;
                                    min-width: 100px !important;
                                    margin-right: 10px;
                                }
                            }

                            &.input-li {
                                width: 90%;
                                .input-div {
                                    width: 100%;
                                }
                            }

                        }
                    }
                }
            }
        }

    }

    @media screen and (max-width: 450px) {
        .board-container-layout {
            .board-container {
                width: 100%;
                margin: 0;
                padding: 36px 20px 50px 20px;
                .board-title {
                    position: relative;
                    .write-button {
                        position: absolute;
                        right:0;
                        top:-4px;
                        width: 71px;
                        min-width: 71px;
                        .blackButton {
                            min-height: 35px !important;
                            max-height: 35px !important;
                            span {
                                font-size: 14px;
                            }
                        }
                    }
                }
                .search-box {
                    ul {
                        li {
                            &.select-li {
                                width:80px !important;
                                min-width:80px  !important;;

                                &.category {
                                    width: 100px !important;
                                    min-width: 100px !important;
                                    margin-right: 10px;
                                }
                            }

                            &.input-li {
                                width: 90%;
                                .input-div {
                                    width: 100%;
                                }
                            }

                        }
                    }
                }
            }
        }
    }
</style>
