<template>
    <div class="article-box">
        <ul class="article-list">
            <li class="pc-element">
                <div class="subject-content theader">
                    <div class="number">
                        번호
                    </div>

                    <div class="category" v-if="boardCategoryUse">
                        카테고리
                    </div>
                    <div class="main">
                        <div class="subject">
                            제목
                        </div>
                        <div class="author">
                            글쓴이
                        </div>
                        <div class="date">
                            날짜
                        </div>
                        <div class="hit">
                            조회수
                        </div>
                    </div>

                </div>
            </li>
            <li v-for="(item,index) in noticeList" @click="articleView(item)">
                <div class="subject-content notice">
                    <div class="number pc-element">
                        공지
                    </div>
                    <div class="category pc-element" v-if="boardCategoryUse">

                    </div>
                    <div class="main">
                        <div class="subject">
                            <div class="span" v-if="item.open=='no'">
                                <font-awesome-icon style="font-size:12px;color:#666" icon="fa-solid fa-lock"/>
                            </div>
                            <div class="tablet-mobile-element notice-text">[공지]</div>
                            <div class="subject-text">{{ item.subject }}</div>
                            <div class="span paddng-left" v-if="item.commentCnt>0">[{{item.commentCnt}}]</div>
                            <div class="span paddng-left" v-if="item.dfile"><img src="../../../../assets/img/file.gif">
                            </div>
                        </div>
                        <div class="author-box">
                            <div class="author"><span class="tablet-mobile-element">작성자</span>{{ item.userName }}</div>
                            <div class="date"><span class="tablet-mobile-element">작성일자</span>{{ item.createAt.substring(0, 10)}}</div>
                            <div class="hit pc-element">
                                {{item.hit}}
                            </div>
                        </div>
                    </div>

                </div>
            </li>
            <li v-for="(item,index) in articleList" @click="articleView(item)">
                <div class="subject-content">
                    <div class="number pc-element">
                        {{ item.number }}
                    </div>
                    <div class="category pc-element" v-if="boardCategoryUse">
                        {{ categoryNames[item.category] }}
                    </div>
                    <div class="main">
                        <div class="subject">
                            <div class="span" v-if="item.open=='no'">
                                <font-awesome-icon style="font-size:12px;color:#666" icon="fa-solid fa-lock"/>
                            </div>
                            <div class="tablet-mobile-element category-text" v-if="boardCategoryUse">
                                {{ categoryNames[item.category] }}
                            </div>
                            <div class="subject-text">{{ item.subject }}</div>
                            <div class="span paddng-left" v-if="item.commentCnt>0">[{{item.commentCnt}}]</div>
                            <div class="span paddng-left" v-if="item.dfile"><img src="../../../../assets/img/file.gif">
                            </div>
                        </div>
                        <div class="author-box">
                            <div class="author"><span class="tablet-mobile-element">작성자</span>{{ item.userName }}</div>
                            <div class="date"><span class="tablet-mobile-element">작성일자</span>{{ item.createAt.substring(0, 10)}}</div>
                            <div class="hit pc-element">
                                {{item.hit}}
                            </div>
                        </div>
                    </div>

                </div>
            </li>
        </ul>
    </div>
</template>
<script>

    import UTIL from '@/common/util'
    import JQ from 'jquery'

    export default {
        name: 'boardNormalList',
        props: [
            "articleData",
            "noticeData",
            "categoryNames",
            "boardCategoryUse"
        ],
        watch: {
            articleData(val) {
                this.setArticleList()
            }
        },
        data() {
            return {
                articleList: [],
                noticeList: [],
                util: UTIL,


            };
        },
        created() {
            this.setData();
        },
        methods: {
            setData() {
                this.setArticleList()
            },
            setArticleList() {

                this.noticeList = [];
                for (const item of this.noticeData) {
                    if (item.dfileNames) item.dfile = true;
                    else item.dfile = false;
                    this.noticeList.push(item);
                }

                this.articleList = [];
                for (const item of this.articleData) {
                    if (item.dfileNames) item.dfile = true;
                    else item.dfile = false;
                    this.articleList.push(item);
                }
            },
            articleView(item) {
                this.$emit('boardAction', {type: 'view', item: item})
            },
            registArtice() {
                this.$emit('boardAction', {type: 'write'})
            },

        }

    }
</script>

<style lang="scss" scoped>

    .article-box {
        width: 100%;
        padding: 0;
        margin-top: -5px;

        .article-list {
            padding: 0;
            margin: 0;

            li {
                padding: 0;
                cursor: pointer;
                height: 50px;

                .subject-content {
                    border-bottom: solid 1px #dcdcdc;
                    display: flex;

                    &.notice {
                        border-top: solid 1px #cacaca;
                        background: #fafafa;

                        &:hover {
                            border-top: solid 1px #cacaca;
                            background: #fafafa;
                        }
                    }

                    &:hover {
                        border-top: solid 1px #dcdcdc;
                        background: #fafafa;
                    }

                    .number {
                        min-width: 40px;
                        width: 40px;
                        font-size: 14px;
                        line-height: 50px;
                        color: #333;
                        text-align: center;

                    }

                    .category {
                        min-width: 100px;
                        width: 100px;
                        font-size: 14px;
                        line-height: 50px;
                        color: #000000;
                        text-align: center;
                    }

                    .main {
                        width: 95%;
                        display: flex;

                        .icon {
                            font-size: 14px;
                            margin: 3px 10px 0 0;
                        }

                        .subject {
                            padding-left: 10px;
                            font-size: 14px;
                            line-height: 50px;
                            height: 50px;
                            color: #333;
                            margin: 0;
                            width: 95%;
                            display: flex;
                            justify-content: left;

                            .subject-text {
                                color: #333;
                                font-size: 14px;
                                max-height: 50px;
                                white-space: nowrap;
                                overflow: hidden;
                                -webkit-line-clamp: 1;
                                text-overflow: ellipsis;
                                white-space: normal;
                                word-wrap: break-word;
                                display: -webkit-box;
                                text-align: left;
                                -webkit-box-orient: vertical;
                            }

                            .span {
                                font-size: 14px;
                                color: #333;
                                padding-right: 5px;
                                &.paddng-left {
                                    padding-left: 3px;
                                }
                            }
                        }
                        .author-box {
                            display: flex;
                            .author {
                                min-width: 100px;
                                font-size: 14px;
                                line-height: 50px;
                                color: #333;
                                text-align: center;
                            }

                            .date {
                                min-width: 100px;
                                font-size: 14px;
                                line-height: 50px;
                                color: #333;
                                text-align: center;
                            }

                            .hit {
                                min-width: 50px;
                                font-size: 14px;
                                line-height: 50px;
                                color: #333;
                                text-align: center;
                            }
                        }

                    }

                    &.theader {
                        border-bottom: solid 1px #cacaca;
                        border-top: solid 1px #cacaca;

                        &:hover {
                            border-top: solid 1px #cacaca;
                            background: #fff;
                        }

                        .number {
                            font-weight: bold;

                        }

                        .category {
                            font-weight: bold;
                            line-height: 50px;


                        }

                        .main {
                            .subject {
                                text-align: center;
                                font-weight: bold;

                            }

                            .author {
                                min-width: 100px;
                                font-size: 14px;
                                line-height: 50px;
                                color: #333;
                                text-align: center;
                                font-weight: bold;

                            }

                            .date {
                                min-width: 100px;
                                font-size: 14px;
                                line-height: 50px;
                                color: #333;
                                text-align: center;
                                font-weight: bold;

                            }

                            .hit {
                                min-width: 50px;
                                font-size: 14px;
                                line-height: 50px;
                                color: #333;
                                text-align: center;
                                font-weight: bold;

                            }
                        }
                    }

                }


            }

        }
    }

    @media screen and (max-width: 1259px) {
        .article-box {
            .article-list {

                li {
                    padding: 0;
                    cursor: pointer;
                    height: auto !important;

                    .subject-content {
                        padding:10px 0;
                        &.notice {
                            .notice-text {
                                font-size: 14px;
                                color:#4B74D1;
                                font-weight: bold;
                                margin-right:5px;
                            }
                        }
                        .number {
                            line-height: 30px !important;
                        }
                        .category {
                            line-height: 30px !important;
                        }

                        .main {
                            width: 100%;
                            display: block;

                            .subject {
                                line-height:30px !important;
                                height:auto !important;
                                max-height: 30px !important;
                                .subject-text {
                                    max-height: 30px !important;
                                }
                            }
                            .author-box {
                                padding-top:5px;
                                .author {
                                    min-width:auto !important;
                                    line-height:100% !important;
                                    color: #333;
                                    padding-left:10px;
                                    text-align: left !important;
                                    span {
                                        color: #999;
                                        padding-right:5px;
                                    }
                                }
                                .date {
                                    padding-left:10px;
                                    min-width:auto !important;
                                    line-height:100% !important;

                                    color: #333;
                                    span {
                                        color: #999;
                                        padding-right:5px;
                                    }
                                }

                            }

                        }

                    }


                }

            }
        }


    }

    @media screen and (max-width: 450px) {

    }
</style>