<template>
    <div class="paging-container">
        <div class="paging-row" v-if="pagingView">
            <ul class="paging-ul">
                <li class="d-page" @click="pagingMove('first')">
                    <font-awesome-icon icon="fa-solid fa-angles-left"/>
                </li>
                <li class="d-page" @click="pagingMove('prev')">
                    <font-awesome-icon icon="fa-solid fa-angle-left"/>
                </li>
                <li :class="item.class" v-for="item in pagingLinks" @click="pagingDefaultMove(item.label)">
                    {{ item.label }}
                </li>
                <li class="d-page" @click="pagingMove('next')">
                    <font-awesome-icon icon="fa-solid fa-angle-right"/>
                </li>
                <li class="d-page" @click="pagingMove('last')">
                    <font-awesome-icon icon="fa-solid fa-angles-right"/>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>

export default {
    name: 'paging',
    props: ["paging","reload","addPrams","scrollNone"],
    data() {
        return {
            pagingLinks: [],
            pagingView: false,
            nextPage: '',
            prevPage: '',

        };
    },
    watch: {
        reload(val) {
            if(val) {
                this.setPaging();
            }

        },

    },
    created() {
        this.setPaging();
    },
    methods: {
        setPaging() {
            if (this.paging && this.paging.currentPage) {
                if(this.paging.total<1) {
                    this.pagingView = false;
                    this.pagingLinks = [];

                    return;
                }
                this.pagingView = true;
                this.pagingLinks = [];

                const pagingGroup =  this.paging.total/this.paging.limit;
                const pagingEnd = Math.ceil(pagingGroup);

                let stIndex =  this.paging.currentPage/this.paging.pageGroup;
                const checkIndex = this.paging.currentPage % this.paging.pageGroup;
                stIndex =  Math.floor(stIndex);
                if(checkIndex<1) {
                    stIndex = stIndex -1;
                }
                const startIndex = (stIndex * this.paging.pageGroup)+1;
                let endIndex = startIndex+this.paging.pageGroup;
                let nexPage = 0;
                for (let index=startIndex; index<endIndex; index++) {
                    if(index <= pagingEnd) {
                        const item = {};
                        item.class = (this.paging.currentPage == index) ? 'active' : '';
                        item.label = index;
                        this.pagingLinks.push(item)
                        nexPage = index;
                    }
                }
                const prevPage = startIndex - 1;
                this.prevPage = (prevPage<1)?1:prevPage;
                this.nextPage = nexPage;
                this.lastPage = pagingEnd;
            }
        },
        pagingMove(cmd) {
            let page = 1;
            switch (cmd) {
                case 'first':
                    page = 1;
                    break;
                case 'prev':
                    page = this.prevPage;
                    break;
                case 'next':
                    page = (this.nextPage < this.lastPage) ? (this.nextPage + 1) : this.lastPage;
                    break;
                case 'last':
                    page = this.lastPage;
                    break;
            }

            this.$emit('pageMove', {page: page,addParams:this.addPrams});
            if(this.scrollNone!='yes') {
                scrollTo(0,0)
            }


        },
        pagingDefaultMove(page) {
            this.$emit('pageMove', {page: page,addParams:this.addPrams});
            if(this.scrollNone!='yes') {
                scrollTo(0,0)
            }

        },


    },
    mounted() {

    },
    updated() {
    }

}
</script>

<style lang="scss" scoped>
